#share_modal {
    width:60vw;
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 5vh 5vh 3vh 15vh 5vh;
    grid-row-gap: 2vh;
}

#add_editor_label {
    grid-row: 1 / span 1;
    grid-column: 1 / span 2;
    font-size: 1.5em;
    font-weight: bold;
    align-self: center;
}

#add_editor_field {
    grid-row: 2 / span 1;
    grid-column: 1 / span 2;
    display: flex;
    justify-content: space-between;
    grid-column-gap: 1vw;
    -webkit-column-gap: 1vw;
            column-gap: 1vw;
    /*align-self: center;*/
}

#add_editor_field input{
    flex-grow: 15;
    border: none;
    background-color: #f2f2f2;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    overflow: hidden;
    border: none;
}

#add_editor_field input:focus {
    outline: none !important;
    border-bottom: 2px solid #664C43;
}

#add_editor_field button {
    flex-grow: 1;
    grid-row: 2 / span 1;
    grid-column: 2 / span 1;
    background-color: #C5AA8C;
    border-style: none;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
}

#collaborators_label {
    grid-row: 3 / span 1;
    grid-column: 1 / span 2;
    font-size: 1.2em;
    align-self: center;
}

#collaborators_list {
    grid-row: 4 / span 1;
    grid-column: 1 / span 2;
    overflow-y: auto;
    font-size: 1em;
}

.collaborator{
    display:grid;
    grid-template-columns: 50vw 10vw;
    grid-template-rows: 1fr;
}

.collaborator:hover{
    background-color: rgb(239, 239, 239);
}

.collaborator_user_email{
    grid-row: 1 / span  1;
    grid-column: 1 / span 1;
    max-width: 40vw;
    overflow: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.collaborator_user_email::-webkit-scrollbar {
   display: none;
}

.owner_label {
    grid-row: 1 / span  1;
    grid-column: 2 / span 1;
    justify-self: end;
}

.remove_button {
    grid-row: 1 / span  1;
    grid-column: 2 / span 1;
    justify-self: end;
    color: #664C43;
    font-family: 'Montserrat', sans-serif;
    border: none;
    cursor: pointer;
    padding: 0!important;
}

.remove_button:hover {
    color: red;
}

#done_button {
    grid-row: 5 / span 1;
    grid-column: 1 / span 2;
    font-family: 'Montserrat', sans-serif;
    justify-self: end;
    background-color: #C5AA8C;
    border-style: none;
    font-weight: bold;
    padding: 0 1vw;
}

/* For Mobile Portrait Mode*/
@media only screen and (max-width: 600px) and (orientation: portrait) {
    #add_editor_label {
        font-size: 1em;
    }

    #collaborators_label {
        font-size: 1em;
    }

    #collaborators_list {
        font-size: 0.8em;
    }
}


/* Mobile Landscape */
@media only screen and (max-height: 600px) and (orientation: landscape) {
    #add_editor_label {
        font-size: 1em;
    }

    #collaborators_label {
        font-size: 1em;
    }

    #collaborators_list {
        font-size: 0.8em;
    }
}


#login-screen {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 16vh 84vh;
    grid-template-columns: 1fr;
    background-color: beige;
}


#app-name {
    font-family: 'Montserrat', sans-serif;
    height: 100%;
    width: 100%;
    grid-row: 1 /span 1;
    grid-column: 1 /span 1;
    font-size: 3em;
    background-color: #C5AA8C;
    display: flex;
    justify-content: center;
    align-items: center;
}

#login-google {
    grid-row: 2 /span 1;
    grid-column: 1 /span 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    margin-bottom: 35vh;
    font-weight: bold;
}

#login-google button {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    border-color: #664C43;
    border-style: solid;
    background-color: #ebe3ca;
    padding: 3vh 3vw;
    font-size: 1.2em;
    cursor: pointer;
}

#login-google button:hover{
    background-color: #C5AA8C;
}
